<template>
    <v-navigation-drawer
            color="primary"
            dark
            app
            class="elevation-5"
            clipped
            :src="getBackground"
            permanent>
        <v-list rounded>
            <v-list-item-group v-model="selectedItem" color="secondary" @change="updateSelection">
                <v-list-item v-for="(item, i) in items" :key="i">
                    <v-list-item-icon>
                        <v-icon v-text="item.icon"></v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                        <v-list-item-title v-text="item.text"></v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list-item-group>
        </v-list>
    </v-navigation-drawer>
</template>

<script>
    export default {
        props: ['items'],
        name: "Menu",
        data() {
            return {
                selectedItem: 0,
            };
        },
        computed: {
            getBackground() {

                if (this.items[this.selectedItem].background) {
                    return this.items[this.selectedItem].background;
                }

                return undefined;
            }
        },
        methods: {
            updateSelection() {
                this.$emit('input', this.items[this.selectedItem]);
            }
        }
    }
</script>

