<template>
    <div>
        <v-row justify="center"
               align="center">
            <v-col cols="12">
                <v-stepper
                        v-model="step"
                        vertical
                        non-linear>
                    <v-stepper-step editable edit-icon='$complete' :complete="step > 1" step="1">
                        Who to pickpocket?
                        <small v-if="selectedNpc" v-text="selectedNpc.name"></small>
                    </v-stepper-step>
                    <v-stepper-content step="1">
                        <v-select
                                v-model="selectedNpc"
                                :items="npcs"
                                label="Select"
                                solo
                        >
                            <template v-slot:selection="data">
                                <template v-if="typeof data.item !== 'object'">
                                    <v-list-item-title v-text="data.item"></v-list-item-title>
                                </template>
                                <template v-else>
                                    <v-avatar left tile>
                                        <v-img :src="data.item.avatar"></v-img>
                                    </v-avatar>
                                    <v-list-item-content>
                                        <v-list-item-title v-html="data.item.name"></v-list-item-title>
                                    </v-list-item-content>
                                </template>
                            </template>
                            <template v-slot:item="data">
                                <template v-if="typeof data.item !== 'object'">
                                    <v-list-item-title v-text="data.item"></v-list-item-title>
                                </template>
                                <template v-else>
                                    <v-list-item-avatar tile>
                                        <v-img :src="data.item.avatar"/>
                                    </v-list-item-avatar>
                                    <v-list-item-content>
                                        <v-list-item-title v-html="data.item.name"></v-list-item-title>
                                    </v-list-item-content>
                                </template>
                            </template>
                        </v-select>
                        <v-btn color="primary" @click="step = 2">Continue</v-btn>
                    </v-stepper-content>

                    <v-stepper-step editable edit-icon='$complete' :complete="step > 2" step="2">
                        Where?
                        <small v-text="selectedLocation"></small>
                    </v-stepper-step>
                    <v-stepper-content step="2">
                        <v-select
                                v-model="selectedLocation"
                                :items="getLocation"
                                label="Select"
                                solo
                        >
                        </v-select>
                        <v-btn color="primary" @click="step = 3">Continue</v-btn>
                    </v-stepper-content>

                    <v-stepper-step editable edit-icon='$complete' :complete="step > 3" step="3">
                        What to loot and what to drop?
                        <small>Keep 14 items, drop none</small>
                    </v-stepper-step>
                    <v-stepper-content step="3">
                        <v-card class="mb-12" height="200px"></v-card>
                        <v-btn color="primary" @click="step = 4">Continue</v-btn>
                    </v-stepper-content>

                    <v-stepper-step editable edit-icon='$complete' :complete="step > 4" step="4">
                        What to do when your inventory is full?
                        <small>Bank</small>
                    </v-stepper-step>
                    <v-stepper-content step="4">
                        <v-card class="mb-12" height="200px"></v-card>
                        <v-btn color="primary" @click="step = 5">Continue</v-btn>
                    </v-stepper-content>

                    <v-stepper-step editable edit-icon='$complete' :complete="step > 5" step="5">
                        Optional: Gloves of Silence, Dodgy Necklace & Seedbox
                        <!--<small>Optional</small>-->
                    </v-stepper-step>
                    <v-stepper-content step="5">
                        <v-card class="mb-12" height="200px"></v-card>
                    </v-stepper-content>

                    <v-btn color="primary float-right">Start pickpocketing!</v-btn>

                </v-stepper>

            </v-col>
        </v-row>
    </div>
</template>

<script>
    export default {
        name: "Pickpocketing",

        data() {
            return {
                step: 0,
                selectedNpc: null,
                selectedLocation: null,
                npcs: [
                    {
                        name: "Man / Woman",
                        avatar: "https://oldschool.runescape.wiki/images/b/b3/Man_chathead.png?ca0cb",
                        locations: [
                            "Lumbridge",
                        ]
                    },
                    {
                        name: "Farmer",
                        avatar: "https://oldschool.runescape.wiki/images/d/d2/Farmer_%28Hosidius%29_chathead.png?62919"
                    },
                    {
                        name: "Female H.A.M.",
                        avatar: "https://oldschool.runescape.wiki/images/9/94/H.A.M._Member_%28female%29_chathead.png?7988c"
                    },
                    {
                        name: "Male H.A.M.",
                        avatar: "https://oldschool.runescape.wiki/images/2/2d/H.A.M._Member_%28male%29_chathead.png?7bb02"
                    },
                    "Warrior",
                    "Rogue",
                    {
                        name: "Master farmer",
                        avatar: "https://oldschool.runescape.wiki/images/5/5e/Master_Farmer_chathead.png?5ba1b"
                    },
                    "Guard",
                    {
                        name: "Fremennik citizen",
                        avatar: "https://oldschool.runescape.wiki/images/7/76/Agnar_chathead.png?34def"
                    },
                    "Knight of Ardougne",
                    "Yanille Watchman",
                    "Paladin",
                    {
                        name: "Gnome",
                        avatar: "https://oldschool.runescape.wiki/images/e/ec/Gnome_%283%29_chathead.png?7e063"
                    },
                    "Hero",
                    {
                        name: "Elf",
                        avatar: "https://oldschool.runescape.wiki/images/2/22/Cirdan_chathead.png?ffa32"
                    },
                ]
            }
        },
        computed: {
            getLocation() {
                if (this.selectedNpc && this.selectedNpc.locations) {
                    return this.selectedNpc.locations;
                }

                return [];
            }
        }
    }
</script>

<style scoped>

</style>